          //   for(let t_name in machineList){
          //     for(let mac_name in machineList[t_name]){
          //       if(mac == mac_name){
          //         machine_id_list.push(machineList[t_name][mac_name].mac_id)
          //       }
          //     }
          //   }
          // })
          // dgm_id_list = that.filter_dgmId_byMachineName(machine_id_list);
