          // dgmArray.forEach(dgm_name=>{
          //   for(let macId in dgmList){
          //     for(let dgmName in dgmList[macId]){
          //       if(dgm_name == dgmName){
          //         dgm_id_list.push(dgmList[macId][dgmName].dgm_id);
          //         dgm_name_list.push(dgmName);
          //       }
