            keys = position.baseInfo.filter_sources_key
            position.baseInfo.filter_val = change_newVal
            position.baseInfo.filter = find_key(change_newVal, sources, keys)
            /* 更改滤波器的时候改变分析频率 */
            if(position.pos_type==9){//包络阶次测点修改滤波器不需要把采样点数和采样周期重置
               that.update_filter_frequency_spectral_sources(position);
            }
