//设置 列头(公共)
export function setHeaders(type) {
  let header
  if (type == 200) { //长采样（特殊）
    header = [
        '组织名称',
        '机组名称',
        '采集器名称',
        '测点名称',
        '采集方式',
        '是否有效',
        '序号',
        '关联通道',
    ];
    return header
  }
  // 除长采样之外其他测点的共同头部
  header = [
      '组织名称',
      '机组名称',
      '采集器名称',
      '测点名称',
      '关联通道',
      '是否有效',
      '是否报警',
  ];
  header.push(...selectHeader(parseInt(type)))
  //是否加入频段
  setHeaderFrequency(header, type)
  return header

}
