//频段列表头
function setHeaderFrequency(headers, type) {
  //设置频段列表头
  if (type == 3|| type == 15 || type == 4 || type == 8 || type == 16 || type == 9 || type == 6 || type == 11 || type == 12 || type == 13 || type == 17) {
    let obj0 = [], obj1 = [];
    for (let i = 1; i <= 8; i++) {
      obj0.push({ label: `频段${i}`, colspan: 5 })
      obj1.push(...[`频段${i}选择`, `频段${i}-1名称`, `频段${i}-1类型`,`频段${i}-2名称`, `频段${i}-2类型`, `频段${i}起始值(Hz)`, `频段${i}结束值(Hz)`])
    }
    // obj1.push('')
    // headers[0].push(...obj0)
    headers.push(...obj1)
  }

}










/* 表数据部分
 */

//设置columns（公共）（组织名称、机组名称、测点名称、采集器名称、测点类型、通道名称、是否有效、是否报警）
export function setColumns(type, data) {
  let columns
  if (type == 200) { //长采样（特殊）
    columns = [
      {
        data: `t_name`, //组织名称
        editor: 'select',
        readOnly: true,
        selectOptions: row => data[row].t_sources
      },
      {
        data: `mac_me`, //机组名称
        editor: 'select',
        readOnly: true,
        selectOptions: row => data[row].macName_sources,

      },
      {
        data: `dgm_name`, //采集器名称
        editor: 'select',
        readOnly: true,
        selectOptions: row => data[row].dgm_sources,
      },
      { data: `pos_name` }, //测点名称

      {
        data: `baseInfo.method_val`, //采集方式
        editor: 'select',
        readOnly: true,
        selectOptions: row => data[row].baseInfo.method_sources
      },
      {
        data: `pos_activity_val`, //是否有效
        editor: 'select',
        selectOptions: row => data[row].pos_activity_sources
      },
      {
        data: `baseInfo.capture_byId`, //序号
        editor: 'select',
        readOnly: true,
        selectOptions: row => data[row].baseInfo.capture_sources
      },
      {
        data: `baseInfo.capture_chName`, //关联通道
        editor: 'select',
        readOnly: true,
        selectOptions: row => data[row].baseInfo.capture_chName_sources
      },

    ]
    return columns
  }
  // 除长采样之外的数据表部分
  columns = [
    {
      data: `t_name`, //组织名称
      editor: 'select',
      readOnly: true,
      selectOptions: row => data[row].t_sources
    },
    {
      data: `mac_me`, //机组名称
      editor: 'select',
      readOnly: true,
      selectOptions: row => data[row].macName_sources,

    },

    {
      data: `dgm_name`, //采集器名称
      editor: 'select',
      readOnly: true,
      selectOptions: row => data[row].dgm_sources,
    },
    { data: `pos_name` }, //测点名称
    // {
    //     data: `pos_type_name`, //测点类型
    // },
    {
      data: `ch_name`, //通道名称
      editor: 'select',
      selectOptions: row => data[row].ch_sources
    },
    {
      data: `pos_activity_val`, //是否有效
      editor: 'select',
      selectOptions: row => data[row].pos_activity_sources
    },
    {
      data: `isalarm_val`, //是否报警
      editor: 'select',
      selectOptions: row => data[row].isalarm_sources
    },
  ]
  columns.push(...selectColumns(parseInt(type), data))
  //是否加入频段
  setColumnFrequency(columns, type)
  return columns
}
