//vue调用方式
import Handsontable from "handsontable";

import 'handsontable/languages/zh-CN';
//本地调用方式
/*import Handsontable from "./handsontable/handsontable.full";//

import './handsontable/zh-CN';*/
export default function hdtable(el,config){
    // 行头
    config.rowHeaders=true
    // 数据过滤
    config.filters=true
    // 列头下拉菜单
    config.dropdownMenu=['filter_by_value','filter_action_bar']
    // 列排序
    config.columnSorting=true
    // config.contextMenu = true
    // 列隐藏
    // config.hiddenColumns=true
    // 行隐藏
    // config.hiddenRows=true
    //设置表格的高
    config.height= '100%'
    //设置表格的宽度
    config.width='100%'
    // 列宽自适应
    // config.autoColumnSize = {syncLimit: '10%'}
    /* 固定列宽度 */
    // config.colWidths = '200'
    /* 允许拖动改变列宽 */
    config.manualColumnResize = true
    //设置单元格的高
    config.rowHeights= '30px'
    //合并单元格
    // config.mergeCells=true
    //手动固定某一列
    config.manualColumnFreeze = true
    //一次性加载全部行
    // config.renderAllRows=true
    //所有列延伸
    config.stretchH="all"
    //空出多少行
    // config.minSpareRows=5

    config.observeChanges = false
    //禁用或启用CopyPaste插件以提供复制/剪切/粘贴功能
    config.copyPaste= true
    //如果为true，则按TAB最后一列中的或向右箭头将移至下一行的第一列
    config.autoWrapRow=true
    //右下角只允许向下拉动
    config.fillHandle={// enable plugin in vertical direction and with autoInsertRow as false
      autoInsertRow: false,
      direction: 'vertical'
    },

    // 该HiddenRows插件允许隐藏某些行。
    config.hiddenRows= {
      rows: [],
      indicators: true
    },
    // 语言
    config.language='zh-CN'
    // 密钥
    config.licenseKey='non-commercial-and-evaluation'



    config.manualRowMove = true /* 行和行之间能够拖动换行 */
    return Handsontable(el,config)
}

